

import Pagination from "@/shared/components/Pagination/Pagination.vue"
import { defineComponent } from "vue";
import { PAGE_SIZE } from "@/shared/constants/common.const";
import BreadCrumb from "@/shared/components/BreadCrumb/BreadCrumb.vue";
import SelectOption from "@/shared/components/SelectOption/SelectOption.vue";

export default defineComponent({
    name: "icm202001sc00",

    components: {
        Pagination,
        BreadCrumb,
        SelectOption
    },

    data() {
        const faqCategoryCodeList = [] as { categoryCode: string, categoryName: string }[]
        const faqList = [] as []
        const pageSize = PAGE_SIZE as number
        const totalItems = 0 as number
        const currentPage = 1 as number
        const startingIndex = 1 as number
        const isReset = false as boolean
        const searchForm = {
            searchValue: '',
            displayYN: '',
            faqCategoryCode: '',
            statusCode: ''
        }
        const header = [
            { columnName: this.$t('icm202001sc00.column.no') },
            { columnName: this.$t('icm202001sc00.column.view') },
            { columnName: this.$t('icm202001sc00.column.faq_id') },
            { columnName: this.$t('icm202001sc00.column.faq_category') },
            { columnName: this.$t('icm202001sc00.column.faq_title') },
            { columnName: this.$t('icm202001sc00.column.status') },
            { columnName: this.$t('icm202001sc00.column.display') },
            { columnName: this.$t('icm202001sc00.column.created_date') },
        ]

        return {
            faqList,
            searchForm,
            header,
            totalItems,
            pageSize,
            currentPage,
            startingIndex,
            isReset,
            faqCategoryCodeList
        };
    },

    mounted() {
        this.requestFaqcategoryCodeList()
        this.requestFAQInfoList()
    },

    computed: {
        displayYN(): any {
            return this.$store.state.CommonCodeModule.commonCode.YnType
        },
    },

    methods: {

        handleRegister() {
            this.$router.push({
                path: `ICM202001SC01`,
                query: { type: "register" }
            });
        },

        handleClickRow(item) {
            this.$router.push({
                path: `ICM202001SC01`,
                query: {
                    type: "edit",
                    faqID: Number(item.faqID)
                }
            });
        },

        async requestFaqcategoryCodeList() {
            const reqBody = { parentCategoryCode: '004' }
            const res: any = await this.$request.getRequest("ICM20000111", reqBody, false);
            this.faqCategoryCodeList = res.body.list;
        },

        async requestFAQInfoList() {
            const body = {
                pageSize: this.pageSize,
                displayYN: this.searchForm.displayYN,
                searchValue: this.searchForm.searchValue,
                pageNumber: this.currentPage,
                faqCategoryCode: this.searchForm.faqCategoryCode,
                statusCode: this.searchForm.statusCode
            };

            const responseData: any = await this.$request.getRequest("ICM20200111", body, false)
            if (responseData.header.result) {
                this.faqList = responseData?.body?.list.map((item) => {
                    return {
                        faqCategoryCode: item.faqCategoryCode,
                        faqCategoryName: item.faqCategoryName,
                        title: item.title,
                        displayYN: this.$codeConverter.convertValue("YnType", item.displayYN),
                        faqID: item.faqID,
                        firstRegisterDate: this.$datetime.formatStringToStringDate(item.firstRegisterDate),
                        statusCode: `${item.statusCode} : ${this.$codeConverter.convertValue("ContentManangementStatusCode", item.statusCode)}`,
                    }
                });
                this.totalItems = responseData.body.totalCount
            }
        },

        handleClickAction(actionType: string) {
            switch (actionType) {
                case 'register':
                    this.$router.push({
                        path: `ICM202001SC01`,
                        query: { type: "register" }
                    });
                    break;
                case 'search':
                    if (this.currentPage > 1) {
                        this.currentPage = 1
                        this.isReset = true
                        this.requestFAQInfoList();
                    } else {
                        this.requestFAQInfoList();
                    }
                    break;
                default: //case reset
                    this.searchForm.searchValue = this.searchForm.statusCode = this.searchForm.displayYN = this.searchForm.faqCategoryCode = ''
                    this.isReset = true;
                    this.currentPage = 1
                    this.requestFAQInfoList();
                    break;
            }
        },

        handlePagination(pageNumber: number) {
            this.isReset = false;
            this.currentPage = pageNumber
            this.requestFAQInfoList()
        },

        handleStartingIndex(startingIndex: number) {
            this.startingIndex = startingIndex
        }
    }
});
